<template name="el-zoom-in-center">
  <div class="transition-box Con">
    <div class="inner" v-loading = 'loading'>
    <!-- {{this.$route.query.searchVal}} -->
      <ol class="content" v-if="items.length>0">
        <h2>共搜索到与「{{this.searchVal}}」相关的游戏{{ items.length }}个</h2>
        <li v-for="(v,i) in items" :key="i">
          <router-link class="viewBtn" :to="{path:'/detail', query:{'id':v.Id}}">
            <div class="imgCon">
              <img :src="v.Versions[0].Big_icon_url" alt="">
            </div>
            <div class="desCon">
              <div class="btn">
                <router-link class="viewBtn" :to="{path:'/detail', query:{'id':v.Id}}" title="查看">查看</router-link>
              </div>
              <h3>{{v.Name}}</h3>
              <h5>{{parseInt(v.Versions[0].Download_num/10000)}}万人安装</h5>
              <p>{{v.Descrip[0]}}</p>
            </div>
          </router-link>
        </li>
      </ol>
      <div v-else class="noData">
        <img src="./nogame.jpg" alt="">
        <p>
          <router-link to="/">点此返回首页</router-link>
        </p>
      </div>
    </div>

    <!-- <button class="watchMore" @click="loadData()">查看更多</button> -->
  </div>
</template>
<script>
import {search} from '@/api/api'
import { mapGetters } from 'vuex'
export default {
  mounted () {
    this.loadData()
  },
  data: function () {
    return {
      loading: false,
      items: []
    }
  },
  computed: {
    ...mapGetters({
      'gotSearchV': 'gotSearch',
      'searchVal': 'searchVal'
    })
  },
  watch: {
    gotSearchV (a, b) {
      if (a !== b) {
        this.loadData()
      }
    }
  },
  methods: {
    loadData () {
      console.log(this.searchVal)
      this.loading = true
      let params = {
        n: this.searchVal
      }
      try {
        search(params).then(res => {
          if (res.Message === '成功') {
            this.loading = false
            this.items = res.Data
          } else {
            this.loading = false
            this.items = []
          }
        })
      } catch (error) {
        this.loading = false
        this.$message.error('获取数据失败')
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.noData{
  margin:0 auto;
  max-width: 600px;
  img{
    width: 200px;
  }
  p{
    text-align:center;
    height: 80px;
    line-height: 40px;
  }
  a{
    color: #4cc8b5
  }
}
.Con {
  background: #f5f5f5;
  position: relative;
  padding: 50px 0 70px 0;
  .inner {
    background: white;
    .content {
      padding: 30px;
      text-align: left;
      h2 {
        font-size: 16px;
        margin-bottom: 20px;
      }
      li {
        height: 90px;
        margin-bottom: 10px;
        .imgCon {
          float: left;
          width: 70px;
          border-radius: 10px;
          overflow: hidden;
          img{
            width: 62px;
            height: 62px;
          }
        }
        .desCon {
          position: relative;
          margin-left: 80px;
          .btn {
            position: absolute;
            right: 0;
            a.viewBtn {
              float: right;
              width: 64px;
              color: white;
              background: #24aa42;
              text-align: center;
              font-size: 12px;
              height: 26px;
              line-height: 26px;
              border-radius: 4px;
            }
          }
          h3 {
            font-size: 16px;
            color: #333;
            margin: 0;
          }
          h5 {
            font-size: 12px;
            font-weight: normal;
            color: #999;
            margin: 0 2px;
          }
          p {
            font-size: 12px;
            color: #555;
            margin-top: 3px;
            padding-right: 12px;
            display: -webkit-box;
            -webkit-box-orient: vertical;
            -webkit-line-clamp: 2;
            overflow: hidden;
          }
        }
      }
    }
  }
}
</style>



